.circle {
	position: relative;
	width: 100px;
	height: 100px;
}

.circle > img {
	border-radius: 50%;
	width: 90px;
	height: 90px;
	object-fit: cover;
}

.arc_q {
	position: absolute;
	top: 0;
	left: 0;
	width: 90px;
	height: 90px;
	border-radius: 50%;
	border: 5px solid;
	border-color: transparent #22abb2 transparent transparent;
	transform: rotate(-45deg);
	-webkit-transform: rotate(-45deg);
	-moz-transform: rotate(-45deg);
	-ms-transform: rotate(-45deg);
	-o-transform: rotate(-45deg);
}

.arc_cover {
	position: absolute;
	top: 0;
	left: 0;
	width: 60px;
	height: 60px;
	background-color: transparent;
}

