.EmpDashCont {
	padding: 1em;
	padding-top: 3em;
	box-sizing: border-box;
	width: 100%;
	box-shadow: 0px 0px 1px rgba(118, 129, 146, 0.69),
		0px 5px 7px rgba(164, 186, 207, 0.19);
	border-radius: 8px;
}

.SearchJobCard {
	border: 1px solid #d0d8e4;
	border-radius: 8px;
	padding: 1.5em;
	box-sizing: border-box;
}

.statsBadge {
	display: inline-flex;
	justify-content: center;
	gap: 15px;
	align-items: center;
}

.stat {
	font-size: 24.9634px;
	line-height: 30px;
	letter-spacing: -0.03em;
	color: #000000;
}

.statName {
	font-size: 15.1951px;
	line-height: 19px;
	color: #474747;
}

.cardTitleCont {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	margin-bottom: 20px;
	gap: 10px;
}

.cardTitleBlock {
	width: 613px;
	padding-top: 1.5em;
}

.cardTitle {
	font-weight: bold;
	font-size: 20px;
	line-height: 24px;
	color: #212121;
	margin-bottom: 5px;
}

.cardSchool {
	font-weight: 500;
	font-size: 16px;
	line-height: 20px;
	letter-spacing: 0.03em;
	color: #4d4d4d;
}

.cardStatsCont {
	display: inline-flex;
	gap: 25px;
	flex-wrap: wrap;
}

.cardDraft {
	background: #0667c3;
	border-radius: 50px;
	padding: 14px 39px;
	border: none;
	cursor: pointer;
	font-weight: 600;
	font-size: 18px;
	line-height: 22px;
	text-align: center;
	color: #f8fafc;
}

.cardBottomCont {
	margin-top: 20px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
}

.cardBottomInfo {
	width: 155px;
}

.cardBottomPosted {
	font-weight: 500;
	font-size: 14px;
	line-height: 17px;
	color: #a6a6a6;
}

.cardBottomExpire {
	font-weight: 500;
	font-size: 14px;
	line-height: 17px;
	color: #4d4d4d;
  margin-top: 5px;
}

.cardBottomC2a {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 264px;
}

.c2aIcon {
	padding: 10px 12px;
	display: inline-flex;
  align-items: center;
	gap: 6px;
	font-weight: 600;
	font-size: 12px;
	line-height: 18px;
	color: #0667c3;
  cursor: pointer;
}

.activeStats {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
	gap: 10px;
  margin-bottom: 50px;
}

.postedJobCard {
	border-radius: 8px;
	background-color: #E1F0FF;
	padding: 1.5em;
}

.postedTitle {
	font-weight: bold;
	font-size: 16px;
	line-height: 24px;
	color: #161D26;
	margin-bottom: 5px;
}

.postedSubtitle {
	font-weight: 500;
	font-size: 14px;
	line-height: 17px;
	color: #636363;
}

.postedTags{
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	min-width: 80%;
	gap: 10px;
}

.profileTag {
	padding: 9.2px 12px;
	border: 1px solid #e4e8f1;
	box-sizing: border-box;
	border-radius: 50px;
	font-size: 12px;
	line-height: 15px;
	letter-spacing: 0.02em;
	color: #33334e;
	display: inline-flex;
	gap: 10px;
	justify-content: center;
}

.aboutTitle {
	font-weight: 800;
	font-size: 14px;
	line-height: 24px;
	margin-bottom: 5px;
}

.aboutDesc {
	font-size: 14px;
	color: #636363;
}

.headerBtn:hover{
	background: #0667C3 !important;
	color: #fff !important;	
}

.cardHeader {
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	min-width: 90%;
}

.cardActions {
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	min-width: 40%;
}

.featuredCandidate {
	border: 1px solid #d0d8e4;
	border-radius: 8px;
	padding: 1.5em;
	box-sizing: border-box;
	display: inline-block;
}

.recommendedCandidate {
	border: 1px solid #d0d8e4;
	border-radius: 8px;
	padding: 1.5em;
	box-sizing: border-box;
	display: inline-block;
}

.contactDiv {
	display: flex;
	justify-content: right;
	padding-top: 1em;
}

.featuredCandidates {
	padding-left: 10vh;
	padding-right: 10vh;
}

.recommendedCandidates {
	padding-left: 10vh;
	padding-right: 10vh;
}