.notificationDD {
	padding: 20px;
	box-sizing: border-box;
	width: 360px;
}

.notiDDHeader {
	margin-bottom: 20px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.notiDDTitle {
	font-weight: 600;
	font-size: 16px;
	line-height: 24px;
	/* identical to box height, or 150% */

	/* Neutral/900 */

	color: #181818;
}

.notiDDsubTitle {
	font-size: 14px;
	line-height: 19px;
	/* identical to box height */

	text-align: right;

	/* Black Neutral/300 */

	color: #9c9c9c;
	cursor: pointer;
}

.notiC2a {
	margin-top: 12px;
	font-size: 14px;
	line-height: 19px;
	/* identical to box height */

	text-align: center;

	/* Neutral/900 */

	color: #181818;
	cursor: pointer;
}

.notification {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	gap: 12px;
	padding: 16px 0;
	box-sizing: border-box;
	width: 100%;
	border-top: 1px solid #dfe8f4;
}

.notiImg {
	width: 40px;
	height: 40px;
	border-radius: 50%;
}

.notiBodyHead {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 8px;
}

.notiName {
	font-weight: 600;
	font-size: 14px;
	line-height: 20px;
	/* identical to box height, or 143% */

	/* Neutral/900 */

	color: #181818;
}

.notiTime {
	font-size: 12px;
	line-height: 16px;
	letter-spacing: 0.02em;

	/* Black Neutral/300 */

	color: #9c9c9c;
}

.notiDesc {
	font-size: 14px;
	line-height: 19px;

	/* Neutral/500 */

	color: #636363;
}
