.contact {
	color: #0667c3;
	font-weight: 500;
	width: 91px;
	height: 34px;
	font-size: 14px;
	border: 1px solid #0667c3;
	border-radius: 20px;
	background: #fff;
}
.contact:hover {
	background: #0667c3;
	color: #fff;
}

.recommandModal {
	width: 90vw;
	height: 90vh;
	overflow: auto;
	padding: 0 !important;
	background: transparent !important;
}

.recommandModal::-webkit-scrollbar {
	display: none;
}

.recommendedJobsCard:hover {
	background: #edf5ff !important;
	border: 2px solid #c7d5e7;
	box-shadow: 0px 0px 2px rgba(98, 95, 130, 0.08), 0px 6px 11px #e7f0fc;
	border-radius: 12px;
}

.contactBtn:hover {
	background: #0667C3 !important;
	color: #fff !important;
}
